body,
html {
  height: 100%;
  width: 100%;
}

body {
  background: #d0e8f2;
  font-family: "Architects Daughter", cursive;
}

* {
  padding: 0;
  margin: 0;
}

.ui.input > input {
  font-family: "Architects Daughter", cursive;
  font-size: 1.2rem;
}

.ui.icon.input > i.circular.icon {
  top: 0.55em;
}

.input-field {
  margin-bottom: 50px;
}

.ui.input > input {
  color: #456268;
}

i.inverted.bordered.icon,
i.inverted.circular.icon {
  background-color: #456268 !important;
}

.recipes-list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.ui.input {
  margin-top: 20px;
}

.ui.card,
.ui.cards > .card {
  background: #79a3b1;
  color: #fcf8ec;
  padding: 10px;
}

.ui.cards a.card:hover,
.ui.link.card:hover,
.ui.link.cards .card:hover,
a.ui.card:hover {
  background: #456268;
}

.header {
  font-size: 20px;
  padding: 10px;
}

.ui.modal {
  font-size: 1.2rem;
  color: #456268;
}

.health-labels {
  font-size: 1rem;
}

.health-labels span {
  padding-right: 10px;
  color: #79a3b1;
}

.ui.cards a.card:hover,
.ui.link.card:hover,
.ui.link.cards .card:hover,
a.ui.card:hover {
  transform: none;
}

.ui.positive.button,
.ui.positive.buttons .button {
  background-color: #79a3b1;
  color: #fcf8ec;
}

.ui.positive.button:hover,
.ui.positive.buttons .button:hover {
  background-color: #456268;
}

.ui.modal > .header {
  font-family: "Architects Daughter", cursive;
  color: #456268;
}

#top-button {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  cursor: pointer;
}

.ui.teal.button,
.ui.teal.buttons .button {
  background-color: #456268;
  color: #fcf8ec;
}

.ui.teal.button:hover,
.ui.teal.buttons .button:hover {
  background-color: #fcf8ec;
  color: #456268;
}

.ui.table {
  margin: 0;
  color: #456268;
}

.recipe-url {
  margin-top: 20px;
}

.recipe-url a {
  font-weight: 600;
  font-size: 18px;
  color: #79a3b1;
}

@media (max-width: 500px) {
  .header {
    font-size: 15px;
  }
}
